import Vue from "vue";

// IntersectionObserver를 이용한 방식
Vue.directive("scrollanimation", {
  // eslint-disable-next-line no-unused-vars
  bind(el) {
    // 고유 ID 생성 (요소별 상태를 저장하기 위해 필요)
    const elementId = el.dataset.scrollanimationId || Math.random().toString(36).substr(2, 9);
    el.dataset.scrollanimationId = elementId;

    // 애니메이션 초기 상태 (fade-in) 추가
    el.classList.add("fade-in");

    // 애니메이션 처리 함수
    const handleAnimation = () => {
      const rect = el.getBoundingClientRect();
      const windowHeight = window.innerHeight || document.documentElement.clientHeight;

      if (
        (rect.top >= 0 && rect.top <= windowHeight) ||
        (rect.bottom >= 0 && rect.bottom <= windowHeight) ||
        (rect.top < 0 && rect.bottom > windowHeight)
      ) {
        el.classList.add("active");
      }
    };

    // IntersectionObserver 콜백 함수
    const observerCallback = (entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          entry.target.classList.add("active");
          observer.unobserve(entry.target);
        }
      });
    };

    // IntersectionObserver 생성
    const observer = new IntersectionObserver(observerCallback, {
      threshold: 0.5,
    });

    observer.observe(el);

    // 옵저버 참조를 el에 저장
    el.__scrollanimationObserver__ = observer;

    // 이벤트 리스너 참조 저장
    el.__scrollanimationListeners__ = {
      load: handleAnimation,
      scroll: handleAnimation,
      resize: handleAnimation,
      beforeunload: () => {
        el.classList.remove("active");
        el.classList.add("fade-in");
      },
    };

    // 이벤트 등록
    window.addEventListener("load", handleAnimation);
    window.addEventListener("scroll", handleAnimation);
    window.addEventListener("resize", handleAnimation);
    window.addEventListener("beforeunload", el.__scrollanimationListeners__.beforeunload);
  },

  unbind(el) {
    // 이벤트 리스너 제거
    const listeners = el.__scrollanimationListeners__;
    if (listeners) {
      window.removeEventListener("load", listeners.load);
      window.removeEventListener("scroll", listeners.scroll);
      window.removeEventListener("resize", listeners.resize);
      window.removeEventListener("beforeunload", listeners.beforeunload);
      delete el.__scrollanimationListeners__;
    }

    // 옵저버 제거
    if (el.__scrollanimationObserver__) {
      el.__scrollanimationObserver__.disconnect();
      delete el.__scrollanimationObserver__;
    }
  },
});
